<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <a @click="()=>{$router.go(-1)}">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>路线详情</span>
    </mu-appbar>
    <div class="content">
      <div id="container" :style="heightMap"></div>
    </div>
  </div>
</template>
<script>
// 待完成有marker和视图窗口




var map = null;
const h = document.documentElement.clientHeight || document.body.clientHeight;
const heightDevice = Math.floor(h - 56);
export default {
  data() {
    return {
      heightMap: "height:" + heightDevice + "px"
    };
  },
  mounted() {
    this.initMap();
  },
  methods: {
    initMap() {
      map = new AMap.Map("container", {
        zoom: 11, //级别
        center: [116.397428, 39.90923], //中心点坐标
        viewMode: "2D", //使用3D视图
        mapStyle: "amap://styles/light"
      });
    }
  }
};
</script>
<style scoped>
#container {
  height: 800px;
  width: 100%;
}
</style>


